import React from 'react';
import agent from '../../../agent';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import Banner from '../../Home/Banner';
import './post-list.css';
import './post.css';
import './header.css';
import './footer.css';

const mapStateToProps = state => ({
      appName: state.common.appName,
      data : state.tintChallenge.data,
      filteredData : state.tintChallenge.visibleData,
      filter : state.tintChallenge.filter,
      log: console.log('tintChallenge state is: ',state)
});

const mapDispatchToProps = dispatch => ({
    onLoad: (payload) => {
        var action = { type: 'TINT_CHALLENGE_DATA_LOADED', payload };
        console.log('in "onLoad" mapping',action);
        return dispatch(action);
    },
    onFilterChange: (value) => {
        var action = {type:'FILTER_CHANGED', value};
        console.log('in "onFilter" mapping',value);
        return dispatch(action);
    }
});

class TintChallenge extends React.Component {
    constructor() {
        super();
        this.onChangeFilter = ev => this.props.onFilterChange(ev.target.value);
    }
    componentWillMount()  {
        console.log('TintChallenge:componentWillMount');
        this.props.onLoad(agent.Tint.json());
    }

    render() {
        var TintChallenge = [];
        console.log('TintChallenge.props', this.props);
        var dataToUse = 
            (this.props.filter && this.props.filter.length)
            ?   this.props.filteredData
            :   this.props.data;
        if(dataToUse) {
            dataToUse.map(
                (item) => {
                    // console.log('rendering item',item);
                    var postClassName = 'post '+item.network;
                    {/* make Post into another component*/}
                    TintChallenge.push((
                        <div className="item" key={item.id}>
                            <div className={postClassName}>
                                <div className="header">
                                    <div className="checkbox">
                                    </div>
                                    <div className="actions">
                                        <img src="/img/star-icon.png"/>
                                        <img src="/img/pin-icon.png"/>
                                        <img src="/img/tag-icon.png"/>
                                    </div>
                                    <div className="more-btn">
                                        <img src="/img/more-icon.png"/>
                                    </div>
                                    <div className="check-btn">
                                        <img src="/img/check-icon.png"/>
                                    </div>
                                </div>
                                <div className="image-container">
                                    <img src={item.imgUrl}/>
                                </div>
                                <div className="tags">
                                    <div className="tag tag-foo">foo</div>
                                    <div className="tag tag-bar">bar</div>
                                </div>
                                <div className="content">
                                    {item.title}
                                </div>
                                <div className="footer">
                                    <div className="author">
                                        <img src={item.author.imgUrl}/>
                                        <span className="name">
                                            {item.author.name}
                                        </span>
                                        <span className="username">
                                            @{item.author.username}
                                        </span>
                                        <div className="share-btn">
                                            <img src="/img/share-icon.png"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ));
                }
            );
        }
        return (
              <div className="challenges-page page">
                <Banner appName={this.props.appName} />
                <div className="title">
                    Challenge for Tony
                </div>
                <div className="filter-bar">
                    Search Posts Here:
                    <br/>
                    <input type="text" onChange={this.onChangeFilter}/>
                </div>
                <div className="challenge-container">
                    <div id="posts">
                        <div className="columns">
                            {TintChallenge}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TintChallenge);


