export default (state = {}, action) => {
    switch (action.type) {
        case 'TINT_CHALLENGE_DATA_LOADED':
            console.log('TINT_CHALLENGE_DATA_LOADEDfil',action);
            return { ...state, data: action.payload.data };
        case 'FILTER_CHANGED' :
            console.log('FILTER_CHANGED - will filter', action,state);
            var visibleData = state.data.filter(
                function(item) {
                    console.log("searching for "+action.value);
                    return (item.title.indexOf(action.value) != -1);
                }
            );

            return { ...state, 
                filter : action.value,
                visibleData : visibleData
            };
        default:
            return state;
    }
};


