import React from 'react';
import agent from '../../../agent';
import { connect } from 'react-redux';

import './styles.css';

const mapStateToProps = state => ({
  resume : state.resume,
  log : console.log('tag list state',state)
});

const mapDispatchToProps = dispatch => ({
  onLoad: (payload) => {
        var action = { type: 'RESUME_DATA_LOADED', payload };
        console.log('in "onLoad" mapping',action);
        return dispatch(action);
    }
});

class TagList extends React.Component {
  componentWillMount() {
    console.log('taglist props',this.props);
  }

  render() {
    var tags = [];
    if(this.props.resume && this.props.resume.data && this.props.resume.data.tags) {
      this.props.resume.data.tags.map(
        (tag) => {
          console.log('processing tag',tag);
          tags.push(
            <div className="tag">{tag}</div>
          )
        }
      )
    }
    return (
        <div className="tag-list">
            {tags}
        </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TagList);



