import React from 'react';
import { connect } from 'react-redux';
import './home.css';

const mapStateToProps = state => ({
  articles: state.home.articles
});

const SimpleHomeView = props => {
  return (
    <div className="col-md-9 simple-home-view">
      <div>About Dave</div>
      <div className="intro">
        Lorem ipsum introductum
      </div>
    </div>
  );
};

export default connect(mapStateToProps, () => ({}))(SimpleHomeView);




