import React from 'react';
import agent from '../../../agent';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import Banner from '../../Home/Banner';
import './styles.css';

const mapStateToProps = state => ({
      appName: state.common.appName,
      challenges : state.challengesList.challenges,
      log: console.log('state is: ',state)
});

const mapDispatchToProps = dispatch => ({
    onLoad: (payload) => {
        var action = { type: 'CHALLENGE_LIST_LOADED', payload };
        console.log('in "onLoad" mapping'),action;
        return dispatch(action);
    },
});

class ChallengesList extends React.Component {
    componentWillMount()  {
        console.log('ChallengesList:componentWillMount');
        this.props.onLoad(agent.ChallengesList.all());
    }

    render() {
        var challengesList = [];
        console.log('props.challenge', this.props.challenge, this.props);
        if(this.props.challenges) {
            this.props.challenges.map(
                (challenge) => {
                    console.log('rendering challenge',challenge);
                    challengesList.push(
                        <li>
                            <Link to={challenge.slug}>
                                {challenge.text}
                            </Link>
                        </li>
                    )
                }
            );
        }
        return (
              <div className="challenges-page page">
                <Banner appName={this.props.appName} />
                <div className="challenges-list">
                    <div className="title">
                        Challenges List
                    </div>
                    <ul>{challengesList}</ul>
                </div>
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChallengesList);


