export default (state = {}, action) => {
    switch (action.type) {
        case 'CHALLENGE_LIST_LOADED':
            console.log('CHALLENGE_LIST_LOADED',action);
            return { ...state, challenges: action.payload.challenges };
        default:
            return state;
    }
};


