import React from 'react';

const Banner = ({ appName, token }) => {
  if (token) {
    return null;
  }
  return (
    <div className="banner">
      <div className="container">
        <h1 className="logo-font">
          {appName.toLowerCase()}
        </h1>
        <p><i>The <b>./src</b> is <b>&lt;strong&gt;</b> with <b>this</b> one</i></p>
      </div>
    </div>
  );
};

export default Banner;



