const resumeReducer = function(state = {}, action) {
    switch (action.type) {
        case 'RESUME_DATA_LOADED':
            console.log('RESUME_DATA_LOADED',action);
            var tags = [];
            console.log(action.payload.jobs.length);
            for(var i = 0; i < action.payload.jobs.length; i++) {
                var job = action.payload.jobs[i];
                console.log(i,job);
                for(var j = 0; j < job.skills.length; j++) {
                    var skill = job.skills[j];
                    if(tags.indexOf(skill) == -1) {
                        tags.push(skill);
                    }
                }
            }
            return { ...state, 
                json : {...action.payload},
                data : {
                    tags: tags
                }
            };
        default:
            return state;
    }
};

export default resumeReducer;


